import React from 'react'
import './MainCard.css'

function MainCard(val) {
    return (
        <div className="hos_card" >
            <div className="hos_card_bg">
                <div className="hos_card_text">
                    <h3>{val.hospital_name}</h3>
                    <p>{val.full_address}</p>
                    {/* <hr></hr> */}
                    <div className="hos_card_treatment_rapper">
                    <p className="hos_card_treatment_headline">Treatment</p>
                        <div className="hos_card_treatment_flex">
                            <div className="hos_card_treatment_section1">
                                <p>{val.treatment_1}</p>
                                <p>{val.treatment_2}</p>
                                <p>{val.treatment_3}</p>
                                <p>{val.treatment_4}</p>
                                <p>{val.treatment_5}</p>
                                <p>{val.treatment_6}</p>
                                <p>{val.treatment_7}</p>
                                <p>{val.treatment_8}</p>
                                <p>{val.treatment_9}</p>
                                <p>{val.treatment_10}</p>
                            </div>
                            {/* <hr></hr> */}
                            <div className="hos_card_treatment_section2">
                                <p>{val.treatment_11}</p>
                                {/* <p>asd</p> */}
                                <p>{val.treatment_12}</p>
                                <p>{val.treatment_13}</p>
                                <p>{val.treatment_14}</p>
                                <p>{val.treatment_15}</p>
                                <p>{val.treatment_16}</p>
                                <p>{val.treatment_17}</p>
                                <p>{val.treatment_18}</p>
                                <p>{val.treatment_19}</p>
                                <p>{val.treatment_20}</p>
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
            
       </div>
    )
}

export default MainCard
