import React from 'react'
import './FeedBack.css'
import { useForm } from 'react-hook-form'
import axios from 'axios'
import { useHistory } from 'react-router'
import { useAlert } from 'react-alert'

function FeedBack() {
    const alert = useAlert()
    const { register, handleSubmit } = useForm()
    const history = useHistory()
    const onSubmit = (data) =>{
        console.log(data)
        axios.post('http://localhost:8000/feedback/dataPost',data)
             .then(res=>{
                console.log(res)
                if(res.status === 200){
                  alert.success(res.data.msg)
                }
                if(res.status === 400){
                  alert.error(res.data.msg)
                }
                history.push('/')
             })
             .catch(err=>{
                console.log(err)
             })
    }


    return (
        <div className="contact">
        <div className="contact_polygon"></div>
        <div className="contact_rapper">
          <div className="contact_text">
            <h2>We Welcome Your Questions</h2>
            <h4>Email: email@gmail.com</h4>
          </div>
          <div className="feedback_form">
            <h3>How Was Your Experience ?</h3>
            <p>Demo Line</p>
            <div className="feedback_form_rapper">
              <form onSubmit={handleSubmit(onSubmit)}>
              <div className="ContactName">
                <input type="text" placeholder="Name" {...register('name')} />
              </div>
              <div className="ContactEmail">
                <input type="text" placeholder="Email" {...register('email')}/>
              </div>
              <div className="ContactFeedback">
                <textarea maxLength="300" rows="6" placeholder="FeedBack" {...register('feedback')}></textarea>
              </div>
              <div className="Contact_button_rapper">
                <button className="Contact_button" type="submit">Submit</button>
              </div>
            </form>
            </div>
          </div>
        </div>
      </div>
    )
}

export default FeedBack
