import React from 'react'
import './Home.css'
import { Carousel } from 'react-responsive-carousel';
import logo from '../../Assets/logo.png'
import MainContainer from './MainContainer/MainContainer';

function Home() {
    return (
        <>
            <div className="on_carousel">
                <div className="carousel">
                    <Carousel showStatus={false} showArrows={false} autoPlay={true} infiniteLoop={true} showThumbs={false} showIndicators={false}>
                        <div className="carousel_img">
                            <img src={logo} alt="logo"/>
                        </div>
                        <div className="carousel_img">
                            <img src={logo} alt="logo"/>
                        </div>  
                        <div className="carousel_img">
                            <img src={logo} alt="logo"/>
                        </div>
                    </Carousel>
                </div>
                <div className="heading_text">
                    <p>Providing Information</p>
                    <p>In <span>Emergency Case</span></p>
                    <div className="btn_lern_more">
                       <a href="#filter"><p>Learn more</p></a> 
                    </div>
                </div>
            </div>
            
            <MainContainer/>
        </>
    )
}

export default Home
