import axios from 'axios'
import React, { useEffect, useState } from 'react'
import './FeedBackView.css'

function FeedBackView() {
    const [data,setData] = useState([])

    useEffect(()=>{
        axios.get('http://localhost:8000/feedback/dataGet',{withCredentials:true})
             .then(res=>{
                console.log(res.data)
                setData(res.data.feedbackData)
             })
             .catch(err=>{
                console.log(err)
             })
    },[])

    return (
        <div className="feedback_view_bg">
            <div className="feedback_view_rapper">
            {data.map(el=>(
            <div className="feedback_view_card_rapper" key={el._id}>
                <div className="feedback_view_card">
                    <h2>{el.name}</h2>
                    <h4>Email: {el.email}</h4>
                    <p>{el.feedback}</p>
                </div>
            </div>
            ))}
            </div>
        </div>
    )
}

export default FeedBackView
