import React from 'react'
import { Redirect, Route, Switch } from 'react-router'
import DataEntry from './Component/Admin/DataEntry/DataEntry'
import LogIn from './Component/Admin/LogIn/LogIn'
import SignUp from './Component/Admin/SignUp/SignUp'
import AllInfoCard from './Component/AllInfoCard/AllInfoCard'
import FeedBack from './Component/FeedBack/FeedBack'
import FeedBackView from './Component/FeedBackView/FeedBackView'
import Footer from './Component/Footer/Footer'
import Home from './Component/Home/Home'
import MapView from './Component/MapView/MapView'
import Navber from './Component/Navber/Navber'
import About from './Component/About/About'

function Router() {
    return (
        <>
          <Navber/>
          <Switch>
            <Route exact path="/" component={Home}/>
            <Route path="/cardInfo/:id" component={AllInfoCard}/>
            <Route path="/mapView" component={MapView}/>
            <Route path="/feedback" component={FeedBack} />
            <Route path="/about" component={About} />
            <Route path="/admin/feedbackView" component={FeedBackView}/>
            <Route path="/admin/dataEntry" component={DataEntry} />
            <Route path="/admin/login" component={LogIn} />
            <Route path="/admin/signup" component={SignUp} />
            <Redirect to="/"/>
          </Switch>
          <Footer/>
        </>
    )
}

export default Router
