import React, { useEffect, useState } from 'react'
import './AllInfoCard.css'
// import image from '../../Assets/logo.png'
import {useParams, Link} from 'react-router-dom'
import axios from 'axios'
// import Treatments from './Treatments'

function AllInfoCard() {
    const {id} = useParams()
    const [data, setData] = useState([])
    useEffect(()=>{
        axios.get(`http://localhost:8000/oneHospitalInfo/${id}`)
             .then(async res=>{
                await setData(res.data.detals)
               console.log(res.data.detals)
             })
             .catch(err=>{
                 console.log(err)
             })
    },[id])
    // console.log(data)
    return (
    <>
        <div className="card_info_img">
            <img src={data.img} alt="logo"/>
        </div>
        
        <div className="card_info_details_bg">
            <div className="card_info_details_container">
                <h2>{data.hospital_name}</h2>
                <p>{data.full_address}</p>
                <div className="card_info_section1_flex">
                    <div className="card_info_contact">
                        <h3>Contact</h3>
                        {data.mob_no ? (<>
                            {data.mob_no[0] ? <p>{data.mob_no[0]}</p> :null}
                            {data.mob_no[1] ? <p>{data.mob_no[1]}</p> :null}
                            {data.mob_no[2] ? <p>{data.mob_no[2]}</p> :null}
                        </>) :null}
                        {data.email ? (<>
                            {data.email[0] ? <p>{data.email[0]}</p>: null}
                            {data.email[1] ? <p>{data.email[1]}</p>: null}
                            {data.email[2] ? <p>{data.email[2]}</p>: null}
                        </>):null}
                
                    </div>
                    <div className="card_info_btn_map">
                        <Link to={`/mapView?lat=${data.latitude}&long=${data.longitude}`}><p>Map</p></Link>
                    </div>
                </div>
                <hr className="horizontal_hr"></hr>
                <div className="card_info_treatment">
                <h3>Treatment</h3>
                {data.treatment ? ( 
                <div className="card_info_section2_flex">
                    <div className="card_info_treatment1">
                        <p>{data.treatment[0]}</p>
                        <p>{data.treatment[2]}</p>
                        <p>{data.treatment[4]}</p>
                        <p>{data.treatment[6]}</p>
                        <p>{data.treatment[8]}</p>
                        <p>{data.treatment[10]}</p>
                        <p>{data.treatment[12]}</p>
                        <p>{data.treatment[14]}</p>
                        <p>{data.treatment[16]}</p>
                        <p>{data.treatment[18]}</p>
                       
                    </div>
                    <hr className="vertical_hr"></hr>
                    <div className="card_info_treatment2">
                        <p>{data.treatment[1]}</p>
                        <p>{data.treatment[3]}</p>
                        <p>{data.treatment[5]}</p>
                        <p>{data.treatment[7]}</p>
                        <p>{data.treatment[9]}</p>
                        <p>{data.treatment[11]}</p>
                        <p>{data.treatment[13]}</p>
                        <p>{data.treatment[15]}</p>
                        <p>{data.treatment[17]}</p>
                        <p>{data.treatment[19]}</p>
                    </div> 
                </div>
                ):null}
                </div>
                <hr className="horizontal_hr"></hr>
                <div className="card_info_doctor">
                    <h3>Doctor</h3>
                    {data.doctor ? 
                        (<>
                        {data.doctor[0] ? <p>Dr. {data.doctor[0]}</p>:null}
                        {data.doctor[1] ? <p>Dr. {data.doctor[1]}</p>:null}
                        {data.doctor[2] ? <p>Dr. {data.doctor[2]}</p>:null}
                        {data.doctor[3] ? <p>Dr. {data.doctor[3]}</p>:null}
                        {data.doctor[4] ? <p>Dr. {data.doctor[4]}</p>:null}
                        </>)
                        :null
                    }
                        
                    {/* {data.doctor[0]} */}
                </div>                    
                <hr className="horizontal_hr"></hr>
                <div className="card_info_about">
                    <h3>About</h3>
                    <p className="card_info_about_container">
                        {data.about}
                    </p>
                </div>
                <hr className="horizontal_hr card_info_end_hr"></hr>
            </div>
        </div>
    </>
    )
}

export default AllInfoCard
