import React, { useEffect } from 'react'
import queryString from 'query-string'
import ReactMapGL, {Marker}from 'react-map-gl';
import { useLocation } from 'react-router-dom'

function MapView() {
    const {search} = useLocation()
    const { lat, long } =  queryString.parse(search)
    // console.log(name)

    // console.log(location.search)
    const [viewport, setViewport] = React.useState({
        latitude: 22,
        longitude: 88,
        zoom: 12
      });

    useEffect(()=>{
        setViewport({latitude:parseFloat(lat),longitude:parseFloat(long),zoom:12})
    },[lat, long])  
    //   console.log()
    return (
        <div>
            <ReactMapGL
                {...viewport}
                mapboxApiAccessToken={
                    "pk.eyJ1Ijoid2VibG9vcDAwNyIsImEiOiJja3BmY3pvb20wMjVxMm5tbGs2MnNmcGlrIn0.7twXmO_AG1DfFttdtzEfhg"
                }
                mapStyle="mapbox://styles/mapbox/streets-v11"
                width="100%"
                height="calc(100vh - 70px)"
                onViewportChange={(viewport) => setViewport(viewport)}>
                {lat && long ? (<Marker latitude={parseFloat(lat)} longitude={parseFloat(long)}>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px"  viewBox="0 0 24 24" width="24px" fill="#D0312D">
                        <path d="M0 0h24v24H0z" fill="none"/>
                        <path d="M12 2C8.13 2 5 5.13 5 9c0 5.25 7 13 7 13s7-7.75 7-13c0-3.87-3.13-7-7-7zm0 9.5c-1.38 0-2.5-1.12-2.5-2.5s1.12-2.5 2.5-2.5 2.5 1.12 2.5 2.5-1.12 2.5-2.5 2.5z"/>
                    </svg>
                </Marker>):null}
            </ReactMapGL>
        </div>
        )
}

export default MapView
