import React from "react";
// import images from "../img/unnamed.png";
import "./AboutCard.css";

const AboutCard = ({ img, name, designation }) => {
  return (
    <div className="Team_card_col">
      <div className="Team_card_rapper">
        <div className="Team_card_img">
          <img src={img} alt="logo"></img>
        </div>
        <div className="Team_card_info">
          <h3>{name}</h3>
          <p>{designation}</p>
        </div>
      </div>
    </div>
  );
};

export default AboutCard;
