import React, { useEffect, useState } from 'react'
import MainCard from './MainCard'
import './MainContainer.css'
import axios from 'axios'
import {Link} from 'react-router-dom'
import ReactPaginate from 'react-paginate';

function MainContainer() {
    const [data,setData] = useState([])
    const [totalPage, setTotalPage] = useState(0)
    const [hosName, setHosName] = useState('')
    const [sector, setSector] = useState('')
    const [treatmentSearch, setTreatmentSearch] = useState('')
    const [querySearch, setQuerySearch] = useState('')

    useEffect(()=>{
        axios.get(`http://localhost:8000/getAllData`)
             .then(res=>{
                console.log(res.data)
                setData(res.data.getDetails)
                setTotalPage(Math.ceil((res.data.number_of_item)/10))
             })
             .catch(err=>{
                 console.log(err)
             })
    },[])
    
    const search = () =>{
        let query = ''    
        if(treatmentSearch){
            query = query + `treatment=${treatmentSearch}&`
        }

        if(sector){
            query = query + `sector=${sector}&`
        }

        if(hosName){
            query = query + `name=${hosName}&`
        }
        
        if(query){
            setQuerySearch(query)
        }

        console.log(query)
        axios.get(`http://localhost:8000/getAllData?${query}`)
             .then(res=>{
                console.log(res.data)
                setData(res.data.getDetails)
                console.log(res.data.getDetails.length)
                setTotalPage(Math.ceil((res.data.number_of_item_search)/10))
             })
             .catch(err=>{
                console.log(err)
             })
    }

    const pageChageFunc =({selected})=>{
     
        axios.get(`http://localhost:8000/getAllData?page=${selected+1}&${querySearch}`)
        .then(res=>{
           console.log(res.data)
           setData(res.data.getDetails)
        })
        .catch(err=>{
           console.log(err)
        })
        console.log(selected)
        // console.log(query)
    }

    return (
        <>
        <div id="filter" className="hos_filter_bg">
                <div className="hos_filter">
                    <div className="type">
                        <select className="input_type" onChange={event=>setTreatmentSearch(event.target.value)}>
                            <option value="">Treatment</option>
                            <option value="eye" >Eye</option>
                            <option value="nose">Nose</option>
                            <option value="BBC">BBC</option>
                        </select>
                    </div>
                    <div className="sector">
                        <select onChange={event=>setSector(event.target.value)} className="input_sector">
                            <option value="">Sector</option>
                            <option value="govt">Government</option>
                            <option value="pvt">Private</option>
                        </select>
                    </div>
                    <div className="location">
                        
                        <select className="input_sector">
                            <option value="">Location</option>
                            <option value="kolkata">Kolkata</option>
                            <option value="none">None</option>
                        </select>
                    </div>
                    <div className="hospital_name_search">
                        <input className="input_name_search" placeholder="Hospital Name" value={hosName} onChange={event => setHosName(event.target.value)} type="text" />
                    </div>
                    <div className="search">
                        <input className="search_btn" value="Search" type="button" onClick={search} />
                    </div>
                </div>    
            </div>
        <div className="main_container_bg">
            
            <div className="main_container">
                {data.map(el=>(
                    <Link to={`/cardInfo/${el._id}`} key={el._id}>
                        <MainCard
                            hospital_name = {el.hospital_name}
                            full_address = {el.full_address}
                            treatment_1 = {el.treatment[0]}
                            treatment_2 = {el.treatment[1]}
                            treatment_3 = {el.treatment[2]}
                            treatment_4 = {el.treatment[3]}
                            treatment_5 = {el.treatment[4]}
                            treatment_6 = {el.treatment[5]}
                            treatment_7 = {el.treatment[6]}
                            treatment_8 = {el.treatment[7]}
                            treatment_9 = {el.treatment[8]}
                            treatment_10 = {el.treatment[9]}
                            treatment_11 = {el.treatment[10]}
                            treatment_12 = {el.treatment[11]}
                            treatment_13 = {el.treatment[12]}
                            treatment_14 = {el.treatment[13]}
                            treatment_15 = {el.treatment[14]}
                            treatment_16 = {el.treatment[15]}
                            treatment_17 = {el.treatment[16]}
                            treatment_18 = {el.treatment[17]}
                            treatment_19 = {el.treatment[18]}
                            treatment_20 = {el.treatment[19]}
                        
                        />    
                    </Link>
                ))}
            </div>
            
            {totalPage >1 ? (
            <ReactPaginate
                previousLabel={"Previous"}
                nextLabel="Next"
                containerClassName={"pagination"}
                pageCount={totalPage}
                forcePage={0}
                onPageChange={pageChageFunc}
                activeClassName={"active_page_link"}
                disabledClassName={"disable_page_link"}
            />
            ):null}
        </div>
        </>
    )
}

export default MainContainer
