import axios from 'axios'
import React, { useState } from 'react'
import {useForm} from 'react-hook-form'
import './DataEntry.css'
import { useAlert } from 'react-alert'

function DataEntry() {
    const [img, setImg] = useState("")
    const { register, handleSubmit } = useForm()
    const alert = useAlert()

    const formImg = new FormData()
    formImg.append("file",img)
    formImg.append("upload_preset","medical_img")
    formImg.append("cloud_name","rks")

    const onSubmit =(data)=>{
        console.log(data)
        axios.post('http://localhost:8000/post',data,{withCredentials:true})
             .then(res=>{
                 console.log(res.data.postData)
                 if(res.status === 200){
                     alert.success("Data Posted Successfully")
                 }
                 else{
                     alert.error("Data Not Posted")
                 }
                 axios.post('https://api.cloudinary.com/v1_1/rks/image/upload',formImg)
                      .then(resImg=>{
                          console.log(resImg.data.secure_url)
                          axios.patch(`http://localhost:8000/imgupload/patch/${res.data.postData._id}`,{img:resImg.data.secure_url},{withCredentials:true})
                               .then(resUpd =>{
                                    console.log(resUpd.data)
                                    if(resUpd.status === 200){
                                        alert.success("Image Uploaded Successfully And Saved")
                                    }
                                    else{
                                        alert.error("Image Not Uploaded Successfully")
                                    }
                               })
                               .catch(errUpd =>{
                                   console.log(errUpd)
                               })
                      })
                      .catch(errImg =>{
                          console.log(errImg)
                      })
             })
             .catch(err=>{
                 console.log(err)
             })
    }

    return (
        <div className="data_entry_bg">
            <div className="data_entry_container">
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div className="data_entry_basic_info">
                        <h3>Basic Info</h3>
                        <div className="data_entry_basic_info_flex">
                            <div className="data_entry_basic_info_section1">
                                <input placeholder="Hospital Name" {...register('hospital_name')}/><br></br>
                                <input placeholder="Latitude" {...register('latitude')} /><br></br>
                                <select {...register('sector')}>
                                    <option value="">Sector</option>
                                    <option value="Pvt">Pvt</option>
                                    <option value="Govt">Govt</option>
                                </select>
                            </div>
                            <div className="data_entry_basic_info_section2">
                                <input placeholder="Location" {...register('location')}/><br></br>
                                <input placeholder="Longitude" {...register('longitude')}/><br></br>
                                <select {...register('covid_19')}>
                                    <option value="">COVID-19</option>
                                    <option value="true">Yes</option>
                                    <option value="false">No</option>
                                </select>
                            </div>
                        </div>
                        <textarea rows="3"  placeholder="Full Address" {...register('full_address')}/>
                    </div>
                    <div className="data_entry_contact">
                        <h3>Contact</h3>
                        <div className="data_entry_contact_flex">
                            <div className="data_entry_contact_section1">
                                <input placeholder="Mobile Number 1" {...register('mob_no_1')}/><br></br>
                                <input placeholder="Mobile Number 2" {...register('mob_no_2')}/><br></br>
                                <input placeholder="Mobile Number 3" {...register('mob_no_3')}/>
                            </div>
                            <div className="data_entry_contact_section2">
                                <input placeholder="Email Address 1" {...register('email_1')}/><br></br>
                                <input placeholder="Email Address 2" {...register('email_2')}/><br></br>
                                <input placeholder="Email Address 3" {...register('email_3')}/>
                            </div>
                        </div>
                    </div>
                    <div className="data_entry_treatment">
                    <   h3>Treatment</h3>
                        <div className="data_entry_treatment_flex">
                            <div className="data_entry_treatment_sector1">
                                <input placeholder="Treatment 1" {...register('treatment_1')}/><br></br>
                                <input placeholder="Treatment 2" {...register('treatment_2')}/><br></br>
                                <input placeholder="Treatment 3" {...register('treatment_3')}/><br></br>
                                <input placeholder="Treatment 4" {...register('treatment_4')}/><br></br>
                                <input placeholder="Treatment 5" {...register('treatment_5')}/><br></br>
                                <input placeholder="Treatment 6" {...register('treatment_6')}/><br></br>
                                <input placeholder="Treatment 7" {...register('treatment_7')}/><br></br>
                                <input placeholder="Treatment 8" {...register('treatment_8')}/><br></br>
                                <input placeholder="Treatment 9" {...register('treatment_9')}/><br></br>
                                <input placeholder="Treatment 10" {...register('treatment_10')}/>
                            </div>
                            <div className="data_entry_treatment_sector2">
                                <input placeholder="Treatment 11" {...register('treatment_11')}/><br></br>
                                <input placeholder="Treatment 12" {...register('treatment_12')}/><br></br>
                                <input placeholder="Treatment 13" {...register('treatment_13')}/><br></br>
                                <input placeholder="Treatment 14" {...register('treatment_14')}/><br></br>
                                <input placeholder="Treatment 15" {...register('treatment_15')}/><br></br>
                                <input placeholder="Treatment 16" {...register('treatment_16')}/><br></br>
                                <input placeholder="Treatment 17" {...register('treatment_17')}/><br></br>
                                <input placeholder="Treatment 18" {...register('treatment_18')}/><br></br>
                                <input placeholder="Treatment 19" {...register('treatment_19')}/><br></br>
                                <input placeholder="Treatment 20" {...register('treatment_20')}/>
                            </div>
                        </div>
                    </div>
                    <div className="data_entry_doctor">
                        <h3>Doctor</h3>
                        <div className="data_entry_doctor_flex">
                            <div className="data_entry_doctor_sector1">
                                <input placeholder="Doctor Name 1" {...register('doctor_1')}/><br></br>
                                <input placeholder="Doctor Name 2" {...register('doctor_2')}/><br></br>
                                <input placeholder="Doctor Name 3" {...register('doctor_3')}/>
                            </div>
                            <div className="data_entry_doctor_sector2">
                                <input placeholder="Doctor Name 4" {...register('doctor_4')}/><br></br>
                                <input placeholder="Doctor Name 5" {...register('doctor_5')}/><br></br>
                                <input placeholder="Doctor Name 6" {...register('doctor_6')}/>
                            </div>
                        </div>
                    </div>
                    <div className="data_entry_img">
                        <input type="file" placeholder="Choose Image" onChange={(event)=>setImg(event.target.files[0])} />
                    </div>
                    <div className="data_entry_about">
                        <h3>About</h3>
                        <textarea rows="7" placeholder="Enter All About of Hospital" {...register('about')}/>
                    </div>
                    <div className="data_entry_submit_btn">
                        <input type="submit" value="Submit"/>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default DataEntry
