import React from "react";
import images from "../../Assets/unnamed.png";
import kajol from "../../Assets/kg.jpeg";
import sagnik from "../../Assets/sagnik.jpeg";
import amartya from "../../Assets/amartya.jpeg";
import rahul from "../../Assets/rahul.jpeg";
import "./About.css";
import AboutCard from "./AboutCard";

const About = () => {
  return (
    <div className="about">
      <h2>About</h2>

      <div className="About_rapper">
        <div className="About_selection1">
          <div className="About_selction1_text">
            <h3>What We Are Doing?</h3>
            <p>
              We are providing a vast hospital analysis website with a detailed
              information of doctor name and treatment with a simple and
              attractive UI design.
            </p>
            <h3>What Matters To You (visitor) ?</h3>
            <p>
              In emergency, you will get a detail analytics of hospitals to
              admit your closest person in least time.
            </p>
          </div>
          <div className="About_selection1_image">
            <img src={images} alt="About" />
          </div>
        </div>
        <div className="About_selection2">
          <div className="About_selection2_image">
            <img src={images} alt="About" />
          </div>
          <div className="About_selction2_text">
            <h3>What We Are?</h3>
            <p>
              We are team of four members with a graduation on Bcs CS and we want
              to help our society with a website has a valuable data.
            </p>
            <h3>How You Do It ?</h3>
            <p>
              We visit hospitals and use online source to fullfill the
              details.
            </p>
          </div>
        </div>
      </div>
      <div className="About_team">
        <h2>Team</h2>
        <div className="About_row">
          <AboutCard
            img={sagnik}
            name="Sagnik Biswas"
            designation="Web Developer"
          />
          <AboutCard
            img={kajol}
            name="Kajol Ghosh"
            designation="Web Developer"
          />
          <AboutCard
            img={rahul}
            name="Rahul Banik"
            designation="Web Developer"
          />
          <AboutCard
            img={amartya}
            name="Amartya Barman"
            designation="Web Developer"
          />
        </div>
      </div>
    </div>
  );
};

export default About;
